.bookcase {
  width: 100%;
  height: 150px;
  position: fixed;
  z-index: 0;
  top: 0;
  left: 0;
}
.bookcase .wall {
  width: 100%;
  background: #c2d1cb;
  /* Old browsers */
  background: linear-gradient(to bottom, #c2d1cb 0%, #88a5ad 100%);
  /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#c2d1cb', endColorstr='#88a5ad', GradientType=0);
  /* IE6-9 */
  overflow-x: scroll;
  text-align: center;
  white-space: nowrap;
  position: fixed;
  z-index: 10;
  font-size: 0px;
  letter-spacing: 0px;
  word-spacing: 0px;
}
.bookcase .shelf {
  margin: 0 auto;
  height: 150px;
  width: 100%;
  display: inline-block;
}
.bookcase .img-shelf {
  height: 100%;
  width: 100%;
}
.bookcase-shown {
  animation-name: bookcaseShownAnimation;
  animation-duration: .5s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
}
@keyframes bookcaseShownAnimation {
  0% {
    z-index: 0;
  }
  100% {
    z-index: 1;
  }
}

.generic-transition {
  transition: transform 0.3s, all 0.2s;
}
.opacity-transition {
  transition: display 0.3s, opacity 0.2s;
}
.big-screen-login.slide-off-right,
.big-screen-login.slide-in-right {
  animation-duration: 0.5s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
.big-screen-login.slide-in-right {
  animation-name: slideInRight;
}
.big-screen-login.slide-off-right {
  animation-name: slideOffRight;
}
@keyframes slideInRight {
  0% {
    width: 0%;
    height: 0%;
  }
  100% {
    width: 90%;
    height: 90%;
  }
}
@keyframes slideOffRight {
  0% {
    width: 90%;
    height: 90%;
  }
  100% {
    width: 0%;
    height: 0%;
  }
}
@media screen and (min-width: 500px) {
  @keyframes slideInRight {
    0% {
      right: -500px;
    }
    50% {
      right: 0;
    }
    75% {
      right: 35px;
    }
    100% {
      right: 15px;
    }
  }
  @keyframes slideOffRight {
    0% {
      right: 15px;
    }
    50% {
      right: 35px;
    }
    75% {
      right: 0;
    }
    100% {
      right: -500px;
    }
  }
}

html,
body {
  padding: 0;
  margin: 0;
  min-height: 100%;
  background: #3f3f3f;
  background: linear-gradient(to bottom, #3f3f3f 0%, #2d2d2d 47%, #000000 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#3f3f3f', endColorstr='#000000', GradientType=0);
  /* IE6-9 */
  height: 100%;
}
input {
  -webkit-appearance: none;
}
.container {
  height: 100%;
}
.invisible {
  display: none;
  opacity: 0;
}
.visible {
  opacity: 1 !important;
}
.hide {
  display: none;
}

.desk-container {
  perspective: 1000px;
  height: 100%;
  width: 100%;
}
.animate-desk {
  transform: rotateX(70deg) rotateY(0deg) rotateZ(0deg) scale3d(1, 1, 1) translate3d(0px, 200px, 0px) skew(0deg, 0deg);
  position: absolute;
  z-index: 2;
}
.desk {
  background-image: url(images/wood.jpg);
  background-size: cover;
  width: 100%;
  height: 100%;
  transition: all 0.5s;
}
.desk .logo {
  text-shadow: white 0px 2px 0px;
  font-family: 'Nunito', sans-serif;
  color: #804313;
  opacity: 0.4;
  height: 48px;
  width: 100%;
  font-size: 2em;
  text-align: center;
  padding-top: 5px;
}
@media all and (min-width: 400px) {
  .desk .logo {
    /*font-size: 4em;*/
  }
}

.header {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  position: relative;
}
.header .bookshelf-icon {
  float: left;
  cursor: pointer;
}
.header .bookshelf-icon.visible:hover {
  opacity: 0.9;
}
.header .bookshelf-icon img {
  width: 30px;
  height: 30px;
}
@media screen and (min-width: 500px) {
  .bookshelf-icon {
    top: 20px;
    left: 40px;
  }
}

.login-container {
  float: right;
}
/************************************************************
	The login button which opens the login form
*************************************************************/
.login-btn {
  color: white;
  text-decoration: none;
  cursor: pointer;
  background-color: transparent;
  z-index: 3;
  font-family: 'Raleway', sans-serif;
  font-size: 1em;
  text-shadow: 1px 1px rgba(128, 67, 19, 0.5);
}
.login-btn::before,
.login-btn:after {
  display: inline-block;
  opacity: 0;
  transition: transform 0.3s, opacity 0.2s;
}
@media screen and (min-width: 500px) {
  .login-submit-btn {
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
  }
  .login-btn::before {
    margin-right: 10px;
    content: '[';
    transform: translateX(20px);
  }
  .login-btn::after {
    margin-left: 10px;
    content: ']';
    transform: translateX(-20px);
  }
  .login-btn:hover::before,
  .login-btn:hover::after,
  .login-btn:focus::before,
  .login-btn:focus::after {
    opacity: 1;
    transform: translateX(0px);
  }
}

/************************************************************
	The white modalness thingy
*************************************************************/
.modal-form-component {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 4;
  display: none;
}
.modal-background {
  background-color: white;
  width: 100%;
  height: 100%;
  opacity: 0.9;
}
/************************************************************
	The close button on the form
*************************************************************/
.span-close {
  float: right;
  width: 50px;
  height: 50px;
  margin: 10px 10px 0 0;
  cursor: pointer;
  color: #fff;
  font-size: 48px;
  display: block;
  text-align: center;
  opacity: 1;
  box-sizing: border-box;
}
.span-close:hover::before {
  opacity: 0.9;
}
/************************************************************
	The label for the form
*************************************************************/
.lbl-form {
  color: #fff;
  display: inline-block;
  width: 100%;
  text-align: center;
  font-family: 'Raleway', sans-serif;
}
.hr-form {
  height: 2px;
  border: 0;
  background-color: #488BAE;
}
/************************************************************
	The modal form and sub components
*************************************************************/
.modal-form-wrapper {
  background-color: #303e58;
  width: 90%;
  height: 90%;
  max-width: 600px;
  max-height: 400px;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  position: fixed;
  z-index: 1;
  border-radius: 3px;
  box-shadow: 0px 0px 19px 5px rgba(0, 0, 0, 0.5);
}
.modal-form-wrapper form {
  padding-top: 20px;
}
.modal-form-wrapper .input-wrapper {
  margin-bottom: 20px;
}
.modal-form-wrapper input[type="text"],
.modal-form-wrapper input[type="email"] {
  border: 1px solid black;
  height: 40px;
  border-radius: 3px;
  outline: 0;
  vertical-align: top;
  font-size: 1em;
  padding: 10px;
  display: block;
  margin: 0 auto;
  box-sizing: border-box;
  width: 85%;
  background-color: #dde0e5;
  color: #303e58;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.75);
}
.modal-form-wrapper input[type="text"]:focus,
.modal-form-wrapper input[type="email"]:focus {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
}
/************************************************************
	The submit button on the form
*************************************************************/
.submit-btn {
  background-color: #488BAE;
  color: white;
  padding: 10px;
  outline: none;
  font-weight: bold;
  font-size: 0.9em;
  border: 2px solid #488BAE;
  cursor: pointer;
  width: 85%;
  margin: 0 auto;
  display: block;
  border-radius: 3px;
  box-sizing: border-box;
}
.submit-btn:hover,
.submit-btn:focus {
  background-color: #357596;
}
.submit-btn:active {
  margin-top: 24px;
}

.notepad {
  height: 360px;
  /*width: 600px; TODO: see about getting rid of this */
  width: 96%;
  border: 1px solid #34b2b7;
  background-image: url(images/paperbackground.png);
  position: relative;
  margin: 0 auto;
  box-shadow: 0px 0px 60px 10px rgba(0, 0, 0, 0.17);
  border-radius: 5px;
  max-width: 900px;
}
.notepad .notepad-date {
  font-family: Arial;
  color: #a9a9a9;
  position: absolute;
  top: 10px;
  right: 10px;
}
.notepad .notepad-header {
  background-color: white;
  height: 80px;
  border-radius: 4px;
  position: relative;
}
.notepad .notepad-title {
  bottom: 0;
  left: 20px;
  position: absolute;
  width: 90%;
  height: 40px;
  font-size: 1.5em;
  padding: 40px 0 0 8px;
  outline: none;
  border: 0;
  border-radius: 0;
}
.notepad .pink-divider {
  width: 2px;
  height: 100%;
  position: absolute;
  left: 20px;
  background-color: #f48dac;
  z-index: 1;
}
.notepad .txt-note {
  background-color: red;
  resize: none;
  border: 0;
  outline: none;
  font-size: 2em;
  margin-top: 40px;
  margin-left: 85px;
  position: fixed;
  top: 0;
  left: 0;
}
.notepad .txt-area {
  font-family: Arial;
  color: black;
  position: absolute;
  z-index: 1;
  background-color: transparent;
  /*width: 490px; TODO: see about getting rid of this */
  font-size: 1em;
  outline: none;
  border: 0;
  line-height: 40px;
  word-wrap: break-word;
  resize: none;
  white-space: pre;
  height: calc(100% - 90px);
  width: calc(100% - 40px);
  padding: 10px 0 0 0;
  margin: 0 0 0 30px;
}
.notepad .txt-area.txt-area-hidden {
  color: transparent;
  z-index: 2;
}
.notepad .txt-area.txt-area-hidden::selection {
  color: white;
  text-shadow: 1px 1px white;
  background: #ff1ddc;
}
.notepad .txt-area.txt-area-hidden.ios {
  text-indent: -3px;
}
.notepad .hashtag {
  box-shadow: 0px 0px 0px 2px blue;
  background-color: blue;
  color: white;
  border-radius: 5px;
}
.notepad .caret {
  position: absolute;
  margin-left: -2px;
}
.blink-me {
  animation-name: blinker;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
@keyframes blinker {
  0% {
    opacity: 1.0;
  }
  50% {
    opacity: 0.0;
  }
  100% {
    opacity: 1.0;
  }
}
